.App-header{
  display:flex; background:#090909; color:#fff;
  font-size:0.6rem; padding-left:20px;
}
div{
  box-sizing:border-box;
}
.list{
  padding:16px 20px; 
  border-bottom:1px dashed #ccc;
}
h3 span{
  font-size:0.6rem; font-weight:normal;
}
h3 span:first-child{
  cursor: pointer;
}

.write-box{
  display:flex; justify-content:center;
}
